<template>
  <div class="app-container" v-loading.body="isGettingInfo">

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="line-height: 36px;">会员信息</span>
        <el-button style="float: right;" type="primary" icon="edit" size="mini" @click="edit()"></el-button>
      </div>
      <div class="text item">
        <el-row :gutter="10">
          <el-col :sm="16">
            <el-row :gutter="10">
              <el-col class="info-item" :sm="12">
                <label>姓名: </label>
                <span>{{ memberInfo.name || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>性别: </label>
                <span>{{ memberInfo.sex === 1 ? '男' : '女'}}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>电话: </label>
                <span>{{ memberInfo.mobile || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>微信: </label>
                <span>{{ memberInfo.wechat_code || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>生日: </label>
                <span>{{ formatDateTime(memberInfo.birthday) || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>身份证号: </label>
                <span>{{ memberInfo.id_card || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>家庭地址: </label>
                <span>{{ memberInfo.home_address || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>工作住址: </label>
                <span>{{ memberInfo.work_address || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>来源: </label>
                <span>{{ memberInfo.referer || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>销售: </label>
                <span>{{ memberInfo.sale_staff || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>教练: </label>
                <span>{{ memberInfo.coach_staff || '未设置' }}</span>
              </el-col>
              <el-col class="info-item" :sm="12">
                <label>备注: </label>
                <span>{{ memberInfo.remark || '未设置' }}</span>
              </el-col>
            </el-row>
          </el-col>
          <el-col :sm="8">
            <div class="grid-content user-photo"><img v-if="memberInfo.photo" :src="memberInfo.photo" />
              <span v-else>该会员暂无图片</span>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-card>

    <el-card>
      <div slot="header" class="clearfix">
        <span style="line-height: 36px;">会员卡</span>
        <div style="float: right;">
          <el-button type="primary" size="mini" @click="xuka()">续</el-button>
          <el-button type="danger" size="mini" @click="tingka()">停</el-button>
        </div>
      </div>
      <div class="item">
        <el-row :gutter="10">
          <el-col :sm="12" class="info-item">
            <label>会员卡类型: </label>
            <span>{{ memberInfo.card_type_name + (memberInfo.card_type === 2 ? ' / 剩余' + memberInfo.card_use_num + '次' : '') || '未设置' }}</span>
          </el-col>
          <el-col :sm="12" class="info-item">
            <label>卡片有效期: </label>
            <span>{{ formatDateTime(memberInfo.card_start_time) + '至' + formatDateTime(memberInfo.card_end_time) + ' ' + cardEndTime(memberInfo.card_end_time) || '未设置' }}</span>
          </el-col>
          <el-col :sm="12" class="info-item">
            <label>绑定会员卡物理ID: </label>
            <span>{{ memberInfo.card_id || '未设置' }}</span>
          </el-col>
          <el-col class="info-item" :sm="12">
            <label>所属场地: </label>
            <span>{{ memberInfo.store_id === 1 ? '英威尔海德店' : '英威尔龙珠店'}}</span>
          </el-col>
        </el-row>
        <div class="card-log">
          <div class="title">近期变动</div>
          <el-table
            :data="cardLog"
            stripe
            style="width: 100%">
            <el-table-column
              prop="type"
              label="类型"
              width="100px">
              <template scope="scope">
                <span v-if="scope.row.type === 1" class="log-type xuka">续卡</span>
                <span v-if="scope.row.type === 2" class="log-type tingka">停卡</span>
                <span v-if="scope.row.type === 3" class="log-type kaika">开卡</span>
              </template>
            </el-table-column>
            <el-table-column
              label="详情">
              <template scope="scope">
                <span>{{ formatDetail(scope.row) }}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="remark"
              label="备注">
            </el-table-column>
            <el-table-column
              prop="add_time"
              label="变动时间">
              <template scope="scope">
                <span>{{ moment(scope.row.add_time * 1000).format('YYYY-MM-DD HH:mm') }}</span>
              </template>
            </el-table-column>
          </el-table>

          <div v-show="!listLoading" class="pagination-container">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page" :page-sizes="[5, 10]" :page-size="size" layout="total, sizes, prev, pager, next" :total="total">
            </el-pagination>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import { memberInfo, cardLogList } from "@/api/member";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import router from "@/router";
import { formatDateTime } from "@/utils";

export default {
  data() {
    return {
      cardLog: null,
      listLoading: false,
      // 总条数
      total: null,
      // 页码
      page: 1,
      size: 10
    };
  },
  computed: {
    ...mapGetters(["memberInfo", "isGettingInfo"]),
    cardLogParams() {
      return {
        mobile: this.memberInfo.mobile,
        offset: "0,10",
        type: ""
      };
    }
  },
  watch: {
    memberInfo() {
      this.getCardLog();
    }
  },
  created() {
    const params = {
      mobile: "",
      id: this.$route.params.id
    };
    if (this.memberInfo) {
      this.getCardLog();
    } else {
      this.getMemberInfo(params);      
    }
  },
  methods: {
    ...mapActions({
      getMemberInfo: "member/getInfo"
    }),
    ...mapMutations({
      setMemberInfo: "member/setInfo"
    }),
    msg() {
      this.$message("该功能正在开发！");
    },
    edit() {
      // 设置当前的会员的资料
      this.setMemberInfo(this.memberInfo);
      router.push({ name: "修改会员资料", params: { id: this.memberInfo.id } });
    },
    xuka() {
      console.log('续卡');
      router.push({ name: "会员卡续卡", params: { id: this.memberInfo.id } });
    },
    tingka() {
      console.log('停卡');
      router.push({ name: "会员卡停卡", params: { id: this.memberInfo.id } });
    },
    formatDateTime(time) {
      return formatDateTime(time);
    },
    cardEndTime(time) {
      const now = Math.round(new Date().getTime() / 1000);
      const days = Math.round((time - now) / (60 * 60 * 24));
      return days >= 0 ? "还有" + days + "天到期" : "已到期";
    },
    // 获取续卡停卡流水
    getCardLog() {
      console.log(this.cardLogParams);
      this.listLoading = true;
      cardLogList(this.cardLogParams).then(response => {
        console.log(response);
        this.cardLog = response.data.list;
        this.total = response.data.total;
        this.listLoading = false;
      });
    },
    formatDetail(cardLog) {
      let detail;
      switch (cardLog.type) {
        case 1:
          detail = "续卡" + cardLog.detail.day + "天";
          break;
        case 2:
          detail =
            "停卡 " +
            this.moment(cardLog.detail.stop_start_time * 1000).format('YYYY-MM-DD') +
            " 至 " +
            this.moment(cardLog.detail.stop_end_time * 1000).format('YYYY-MM-DD');
          break;
        case 3:
          detail = "开卡";
          break;
        default:
          detail = "开卡";
      }
      return detail;
    },
    handleSizeChange(val) {
      this.size = val;
      this.cardLogParams.offset = (this.page - 1) * this.size + "," + this.size;
      this.getCardLog();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.cardLogParams.offset = (this.page - 1) * this.size + "," + this.size;
      this.getCardLog();
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss">
.el-col {
  border-radius: 4px;
}

.bg-purple-dark {
  background: #99a9bf;
}

.bg-purple {
  background: #d3dce6;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

.info-item {
  font-size: 14px;
  padding: 5px 0;
  line-height: 20px;
}

.info-item label {
  color: #97a8be;
  font-size: 14px;
}

.el-card {
  margin: 20px 0;
  &:last-of-type {
    margin-bottom: 0;
  }
  &:first-of-type {
    margin-top: 0;
  }
}

.user-photo {
  text-align: center;
  font-size: 14px;
}

.user-photo img {
  max-width: 100%;
  max-height: 300px;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1);
}

.card-log {
  margin-top: 15px;
  .title {
    font-size: 14px;
    line-height: 30px;
    color: #97a8be;
  }
  .log-type {
    display: inline-block;
    font-size: 0.75rem;
    line-height: 1;
    color: #fff !important;
    padding: 2px 5px;
    border-radius: 3px;
    &.xuka {
      background: #1d8ce0;
    }
    &.tingka {
      background: #ff4949;
    }
    &.kaika {
      background: #13ce66;
    }
  }
  .el-pagination {
    margin-top: 5px;
  }
}

@media (min-width: 768px) {
  .card-log {
    .log-item {
      border-bottom: none;
      &:nth-of-type(odd) {
        border-right: 1px dashed #d1dbe5;
      }
    }
  }
}
</style>



